





















export default {
	data() {
		return {
			phone:'',
			code:''
		};
	},
	methods:{
		login(){
			if(this.phone==''){
				uni.showToast({
					title:'请输入手机号'
				})
				return
			}
			if(this.code==''){
				uni.showToast({
					title:'请输入验证码'
				})
				return
			}
			uni.request({
				url:'http://bus.liebianzhe.com/api/login/code_login',
				data:{
					phone:this.phone,
					code:this.code
				},
				success: (res) => {
					console.log(res);
					if(res.data.code==1){
						uni.showToast({
							title:res.data.msg
						})
					}
					else if(res.data.code==2){
						console.log(res)
					}
					else if(res.data.code==0){
						uni.showToast({
							title:'登陆失败'
						})
					}
				}
			})
		},
		getCode(){
			uni.request({
				url:'http://bus.liebianzhe.com/api/login/sendCode',
				data:{
					phone:this.phone
				},
				success: (res) => {
					console.log(res)
				}
			})
		}
	},
	onLoad() {			
	}
}
